<template>
  <form>
    <div>
        <div v-if="aviso==true" class="alert alert-warning" role="alert">
            Preencha todos os campos !
        </div>
      </div>
    <div class="row"> 
      <div class="form-group col-sm-6">
        <label for="ipt1">Nome</label>
        <input id="ipt1" v-model="firstName" type="text" class="form-control form-control-sm" required placeholder="ex: jose">
      </div>
      <div class="form-group col-sm-6">
        <label for="ipt2">Sobrenome</label>
        <input id="ipt2" v-model="lastName" type="text" class="form-control form-control-sm" required placeholder="ex: costa">
      </div>
    </div>
    <div class="row"> 
      <div class="form-group col-sm-6">
        <label for="ipt3">Cpf-validador desligado</label>
        <input id="ipt3" v-model="cpf" type="text" min="11" max="14" class="form-control form-control-sm" required placeholder="ex: 123.123.123-12">
      </div>
      <div class="form-group col-sm-6">
        <label for="ipt4">Aniversario</label>
        <input v-model="birthDate" id="ipt4" type="date" class="form-control form-control-sm" required>
      </div>
      <div class="form-group col-sm-6">
        <label for="ipt5">contato</label>
        <input v-model="phones.number"  id="ipt5" type="text" min="13" max="14" class="form-control form-control-sm" required placeholder="ex: (11)99999-9999">
      </div>

      <div class="form-group col-sm-6">
        <label for="ipt6">Tipo de contato</label>
        <select v-model="phones.type" required class="custom-select custom-select-sm mr-sm-2" id="ipt6">
          <option value="" selected>Escolher...</option>
          <option value="HOME">casa</option>
          <option value="MOBILE">pessoal</option>
          <option value="COMMERCIAL">trabalho</option>
        </select>
      </div>
      <div class="col-sm-12 d-flex justify-content-center">
        <button @click="salvarUser" type="button" class="btn btn-success btn-sm">{{btntitle}}</button>
      </div>
    </div>
  </form>
</template>
<script>
export default {
  props: ['btntitle', 'defaultValues'],
 
  data(){
    
    return {
      aviso: false,
      id: '',
      firstName: '',
      lastName: '',
      cpf: '',
      birthDate: '',
      phones:
        {
          type: '',
          number: '',
        }
      
    }
  },
  computed:{
    rodei(){
      this.$emit('codigo',this.$store.getters.person);
      this.$emit('dados',this.$data);
      return null;
    }
  },
  methods: {
    salvarUser(){

      switch ('') {
        case this.firstName:
        case this.lastName:
        case this.cpf:
        case this.birthDate:
        case this.phones.type:
        case this.phones.number:
          this.aviso = true;
          setTimeout(()=>{this.aviso = false;},5000);
          return;
      }
     
      const dados = {
        id: this.id,
        firstName: this.firstName,
        lastName: this.lastName,
        cpf: this.cpf,
        birthDate: new Date(this.birthDate).toLocaleDateString('pt-br').split('/').join('-'),
        phones: [
          {
            id: this.phones.id,
            type: this.phones.type,
            number: this.phones.number,
          }
        ]
      }
      this.$emit('salve',dados);
      //limpe o form
      if(this.defaultValues!=undefined){
        return;
      }
      this.id = '';
      this.firstName = '';
      this.lastName = '';
      this.cpf = '';
      this.birthDate = '';
      this.phones.type = '';
      this.phones.number = '';
       
    }
  },
  mounted(){
    if(this.defaultValues!=undefined){
      let data = this.defaultValues.birthDate.split('-');
      this.id = this.defaultValues.id;
      this.firstName= this.defaultValues.firstName;
      this.lastName= this.defaultValues.lastName;
      this.cpf= this.defaultValues.cpf;
      this.birthDate= `${data[2]}-${data[1]}-${data[0]}`;
      this.phones = this.defaultValues.phones[0];
      
    }
    this.$emit('codigo', {});
  }
}
</script>