<template>
  <div v-if="aviso.aberto==true" :class="'alert alert-'+aviso.style" role="alert">
    {{aviso.message}}
  </div>
  <FormularioLayout @salve="salve($event)" @codigo="dado = $event" v-if="tela == 'home'" btntitle="registrar pessoa"/>
</template>
<script>
import FormularioLayout from '@/layouts/FormularioLayout.vue'
export default {
  name: 'Home',
  data(){
    return {
      dado: [],
      aviso: {
        aberto: false,
        style: 'success',
        message: "",
      },
    }
  },
  computed: {
    tela(){
      return this.$store.state.tela;
    },
  },
  methods: {
    mudaTela(tela){
      this.$store.commit('mudaTela',tela);
      this.tela = this.$store.state.tela;
    },
    salve(dados){
      this.dado = dados;
      this.$store.commit('SET_CODE', dados);
      delete dados.id;
      delete dados.phones[0].id;

      this.$store.dispatch('createPerson',dados);
      this.aviso.aberto = true;
      this.aviso.message = "Pessoa salva com sucesso!";
      this.$store.dispatch('getPeople');
      setTimeout(()=>{
        this.aviso.aberto = false;
      },6000);
    }
  },
  mounted(){
    this.tela = this.$store.state.tela;
    this.$store.dispatch('getPeople');
  },
  components: {
    FormularioLayout,
  }
}

</script>