import { createStore } from 'vuex'
import api from '@/services/api'

export default createStore({
  state: {
    tela: 'home',
    code: [],
    people: [],
    person: {},
    editedPerson: {},
  },
  getters: {
    people(state){
      return state.people;
    },
    person(state){
      return state.person;
    }
  },
  mutations: {
    mudaTela(state, novo){
      state.tela = novo;
    },
    SET_PEOPLE(state, people){
      state.people = people;
    },
    SET_PERSON(state, person){
      state.person = person;
    },
    SET_PERSON_EDITED(state, editedPerson){
      state.editedPerson = editedPerson;
    },
    SET_CODE(state, code){
      state.code = code;
    }
    // SET_PERSON_DELECTED(state, personDelected){
      
    // },
    // SET_PEOPLE_DELECTED(state, peopleDelected){

    // }
  },
  actions: {
    getPeople({commit}){
      api.get("/people")
      .then((response)=>{
        commit('SET_PEOPLE',response.data)
      })
    },
    updatePeople({commit},data){
      commit('SET_PEOPLE',data);
    },
    createPerson({commit},data){
      api.post("/people", data)
      .then((response)=>{
        commit('SET_PERSON',response.data);
      }).catch((err)=>{
        console.log(err)
      })
      commit, data;
     
    },
    updatePerson({commit},data){
      api.put(`/people/${data.id}`,data)
      .then((response)=>{
        commit('SET_PERSON',response.data);
      }).catch((err)=>{
        console.log(err);
      })
    },
    deletePerson({commit},id){
      api.delete(`/people/${id}`)
      .then((response)=>{
        response;
      }).catch((err)=>{
        console.log(err);
      });
      commit;
    }
  },
  modules: {
  }
})
