<template>
<div class="card-lista">
  <ul>
    <li v-for="person in peopleC" :key="person.id">
      <div class="card">
        <div class="card-body">
          <div class="row">
            <div class="col-sm-6">

              <span><strong>id: </strong>{{person.id}}</span> |
              <span><strong>nome: </strong>{{person.firstName}}</span>

            </div>
            <div v-if="opcao == ''" class="col-sm-6">
              <button type="button" @click="setOpcao('ver'+person.id, person)" class="btn btn-primary btn-sm mr-1">ver</button>
              <button type="button" @click="setOpcao('editar'+person.id, person)" class="btn btn-success btn-sm mr-1">editar</button>
              <button type="button" @click="setOpcao('deletar'+person.id, person)" class="btn btn-danger btn-sm">deletar</button>
            </div>
          </div>
        </div>

          <div v-if="opcao=='ver'+person.id" class="row" style="padding: 10px;">
            <div class="col-sm-6">
              <strong>nome: </strong>
              <span>{{person.firstName}} {{person.lastName}}</span><br>
              <strong>cpf: </strong>
              <span>{{person.cpf}}</span><br>
              <strong>aniversario: </strong>
              <span>{{person.birthDate}}</span><br>
            </div>
            <div class="col-sm-6">
              <strong>numeros</strong>
              <table class="table table-sm">
                <thead>
                  <tr>
                    <th scope="col">id</th>
                    <th scope="col">numero</th>
                   
                  </tr>
                </thead>
                <tbody>
                  <tr  v-for="phone in person.phones" :key="phone.id">
                    <th scope="row">{{phone.id}}</th>
                    <td>{{phone.number}}<br>{{phone.type}}</td>
                  </tr>
                 
                </tbody>
              </table>
            </div>
            <div class="col-sm-12 mt-4">
              <button type="button" @click="setOpcao('')" class="btn btn-danger btn-sm">fechar</button>
            </div>
          </div>

          <div v-if="opcao == 'editar'+person.id" style="padding: 10px;">
            <FormularioLayout 
              @salve="edite($event)"
              btntitle="editar pessoa" 
              :defaultValues="person"/>
            <div class="col-sm-12 mt-4">
              <button type="button" @click="setOpcao('')" class="btn btn-danger btn-sm">fechar</button>
            </div>
          </div>

           <div v-if="opcao == 'deletar'+person.id" style="padding: 10px;">
            <div class="col-sm-12 mt-4 d-flex justify-content-between">
              <button type="button" @click="deletar(person.id)" class="btn btn-success btn-sm">confirmar</button>
              <button type="button" @click="setOpcao('')" class="btn btn-danger btn-sm">cancelar</button>
            </div>
          </div>
      </div>
     
    </li>
  </ul>
  </div>
</template>
<script>
import FormularioLayout from '@/layouts/FormularioLayout.vue'
export default {
  components: {
    FormularioLayout
  },
  data(){
    return {
      opcao: '',
      people: []
    }
  },
  computed: {
    peopleC(){ 
      if(this.people.length<=0){
        this.update(this.$store.getters.people);
      }
      console.log(this.people)
      return this.people;
    }
  },
  methods: {
    update(dados){
      this.people = dados;
    },
    setOpcao(opc, person){
      if(opc===''){
        this.$emit('codigo', this.$store.getters.people);
      }
      if(opc=='ver' || opc=='editar' || opc=='deletar'){
        this.$emit('codigo', person);
      }
      this.opcao = opc;
    },
    edite(dados){
      let pessoas = this.people.filter((e)=>e.id != dados.id);
      this.$store.dispatch('getPeople');
      pessoas.push(dados);
      this.people = pessoas;
      this.$store.dispatch('updatePeople',pessoas);
      this.$store.dispatch('updatePerson',dados);
    },
    deletar(id){
      this.people = this.people.filter((e)=>e.id!=id);
      this.$emit('codigo', this.people);
      alert("Este usuario foi deletado: ID - "+id);
      this.$store.dispatch('deletePerson',id);
      this.$store.dispatch('updatePeople',this.people);
      this.opcao = '';
    }
  },
  mounted(){
    this.$store.dispatch('getPeople');
    this.$emit('codigo',this.people);
  }
}
</script>
<style scoped>
ul{
  list-style: none;
  padding: 0;
}
li{
  margin-top: 6px;
  text-align: left;
}
</style>